<!-- 评论信息页面 -->
<template>
	<u-cell :border="false">
		<u-avatar slot="icon" shape="square" size="40" :src="itemData.url"
			customStyle="margin: 0px 12rpx -3rpx 0rpx"></u-avatar>
		<view slot="title" style="gap: 40rpx;">
			<view class="flexs" style="gap: 24rpx;">
				<view style="font-size: 30rpx;color: #777;">{{ itemData.liaisonName }}</view>
				<!-- <view style="font-size: 26rpx;color: #b1b1b1; flex: 1;" class="flexb">{{ formatPast(itemData.createTime) }}
					<view v-if="showReply" slot="right-icon" style="font-size: 28rpx;color: #999;"
						@tap="$emit('reply', itemData)">
						回复
					</view>
				</view> -->
			</view>
			<view style="font-size: 30rpx;color: #222; margin: 12rpx 0px;"> {{ itemData.messageContent }}
			</view>
			<template v-if="itemData.children">
				<template v-if="itemData.children.length">
					<Record :itemData="itemData.children[0]" @reply="(data)=> $emit('reply', data)"
						:showReply="false" />
					<view v-if="itemData.children.length > 1 && !showMore" class="flexs" style="gap: 4rpx;"
						@tap="showMore = true">
						{{ itemData.children.length - 1 }}条回复
						<u-icon name="arrow-down"></u-icon>
					</view>
					<template v-if="showMore">
						<Record v-for="item in itemData.children.slice(1)" :key="item.id" :itemData="item" :showReply="false" />
					</template>
				</template>
			</template>

		</view>
	</u-cell>
</template>

<script>
	import Record from './record.vue'
	import {
		formatPast
	} from '@/utils/common.js'

	export default {
		components: {
			Record
		},
		props: {
			itemData: {
				type: Object,
			},
			showReply: {
				type: Boolean,
				default: true
			}
		},
		data() {
			return {
				showMore: false
			}
		},
		created() {},
		methods: {
			formatPast,
			recover() {

			}
		},
	}
</script>

